/**
 * 全局通用
 * Created by zhaohuanlei on 2015/4/9.
 */

body, section, article, aside, ul, ol, li, p, h1, h2, h3, h4, h5, h6, img, textarea, input, select, form {
  margin: 0;
  padding: 0;
}

body, input, textarea, select, h1, h2, h3, h4, h5, h6 {
  font: normal 12px/1 Helvetica Neue, Microsoft yahei, arial;
  color: #000;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  color: #000;
  text-decoration: none;
}

em, i {
  font-style: normal;
}

ul, ol {
  list-style: none;
}

img {
  max-width: 100%;
  border: none;
  vertical-align: middle;
}

input, button {
  -webkit-appearance: none;
  outline: none;
}

p {
  word-break: break-all;
}

.c:after {
  content: "";
  display: table;
  clear: both;
}

.clamp {
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  display: -webkit-box;
  -webkit-box-orient: vertical;
}

.hor, .ell, .arrow {
  display: inline-block;
  vertical-align: middle;
}

.arrow {
  width: 0;
  height: 0;
  overflow: hidden;
  border: 6px solid transparent;
}

.ell {
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.hide {
  display: none;
}

.fl {
  float: left;
}

.fr {
  float: right;
}

/*对话框*/
.dialog {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
}

.dialog-overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  background-color: #000;
  opacity: .7;
  //filter: alpha(opacity:70);
}

.dialog-box {
  width: 80%;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -80px 0 0 -40%;
  z-index: 2;
  background-color: #fff;
  border-radius: 5px;
  text-align: center;
}

.dialog-detail {
  padding: 30px 20px;
  font-size: 16px;
  line-height: 1.5;
}

.dialog-opera {
  width: 100%;
  height: 50px;
  border-top: 1px solid #ddd;
}

.dialog-btn {
  height: 50px;
  border: none;
  background: none;
  color: #157efb;
  font-size: 15px;
  line-height: 50px;
  cursor: pointer;
}

.dialog-btn-close {
  width: 100%;
}

.dialog-btn-cancel, .dialog-btn-ok {
  width: 50%;
  float: left;
}

.dialog-btn-ok {
  border-left: 1px solid #ddd;
  box-sizing: border-box;
}

.dialog-prompt {
  max-width: 60%;
  position: fixed;
  top: 45%;
  left: 50%;
  z-index: 1000;
  padding: 15px 20px;
  background-color: rgba(0, 0, 0, .8);
  color: #fff;
  border-radius: 5px;
  line-height: 1.4;
  font-size: 14px;
  transition: opacity .5s;
}

/*1px border*/
.line {
  position: relative;
}

.line:after {
  width: 200%;
  height: 200%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  content: "";
  -webkit-transform: scale(0.5);
  -webkit-transform-origin: 0 0;
  transform: scale(0.5);
  transform-origin: 0 0;
}
